/*--------------------adjustment----------------------*/
*,
*::before,
*::after{
	box-sizing: border-box;
}
html, body{
	margin: 0;
	padding: 0;
}
body{
	font-family: $reg;
	font-weight: normal;
	font-size: 15px;
	min-width: 320px;
	position: relative;
	line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	color: $text;

	.lock & {
		overflow: hidden;
		touch-action: none;
		overflow-behavior: none;
	}
}

